$imgPath: '../../assets/images/';
$transitionDurationNormal: 0.3s;

html {
    margin: 0;
    height: 100%;
    font-size: 1.25vw;
}

body {
    font-family: Lato, "Lucida Grande", Tahoma, sans-serif;
    margin: 0;
    padding: 0;
}

button {
    outline: none;
    border: 0 none;
}

section, div, header, footer, nav, main {
    margin: 0;
    padding: 0;
    position: relative;
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

@media screen and (max-width: $breakpointMedium) {
    html {
        font-size: 12.4px;
    }
}

